<style lang="less" scoped>

    .filter{
        height: 100px;
        padding-top: 20px;
        .casContainer {
            width: auto;
            width: fit-content;
            .casInput {
                width: 250px;
                .ant-input {
                min-height: 32px;
                padding: 0 4px;
                display: flex;
                align-items: center;
                position: relative;

                .right-icon {
                    position: absolute;
                    right: 12px;
                }

                .tags {
                    max-width: 330px;
                    height: auto;
                    word-wrap: break-word;
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;

                    .result {
                    width: 24px;
                    height: 24px;
                    margin-right: 20px;
                    background: #f5f5f5;
                    border-radius: 12px;
                    color: #333;
                    text-align: center;
                    position: absolute;
                    right: 22px;
                    }

                    .casNode {
                    height: auto;
                    background: #f5f5f5;
                    border-radius: 4px;
                    padding: 2px 8px;
                    margin-right: 4px;
                    margin: 2px 4px 2px 0px;
                    color: #333;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .tag {
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        width: auto;
                        margin-right: 4px;
                        display: inline-block;
                        text-align: center;
                    }
                    }
                }
                }
            }
            .casbox {
                position: relative;
                width: fit-content;
                margin-top: 4px;
                display: flex;
                box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.12);
                border-radius: 4px;
                z-index: 100;
                background: #fff;
                .boxItem {
                min-width: 250px;
                padding: 1px 0;
                height: 180px;
                border-right: 1px solid #eee;
                color: #333;

                .Item {
                    height: 34px;
                    text-align: left;
                }

                .checked {
                    .ant-checkbox-wrapper {
                    color: #1bbc9b;
                    }
                }

                &:nth-child(1) {
                    border-radius: 4px 0px 0px 4px;
                }
                &:last-child {
                    border-radius: 0px 4px 4px 0px;
                    border: 0px;
                }
                }
            }

            .icon-arrow{
                transform: rotate(90deg);
                float: right;
            }


            .fadeOut {
                display: none;
            }
            .fadeIn {
                animation: fadeIn 0.25s;
            }

            @keyframes fadeIn {
                from {
                opacity: 0;
                height: 0;
                }
                to {
                opacity: 1;
                height: 180px;
                display: "";
                }
            }
            }
    }
    .ant-row{
        margin-bottom: 10px;
        .ant-col{
            font-size: 13px;
            text-align: center;
            .content{
                background: #FFFFFF;
                box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.12);
                border-radius: 4px;
                height: 120px;
                line-height: 20px;
                position: relative;
                background-size: cover;
            }
            .name{
                margin-top: 10px;
            }
            .del{
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                text-align: center;
                color: red;
                background: rgba(255,255,255,0.5);
                height: 20px;
                line-height: 20px;
            }
           
        }

    }

    .result{
        .list{
            &-line{
                box-shadow: inset 0px -1px 0px 0px #EEEEEE; 
                font-family: PingFangSC-Regular, PingFang SC;
                font-weight: 400;
                color: #333333;
                padding: 12px 24px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .actions{
                    display: flex;
                    align-items: center;
                    margin: 0 0;
                    li{
                    padding: 0 16px; 
                    &:hover{
                        // color: @primary-color;
                    }
                    }

                }
            } 
            .s{
                padding: 8px 16px;
            }
            .g{
                padding: 16px 24px;
            }
        }
    }
     /* box 表单 */
    .flexForm{
        width: auto;
        text-align: center;
        margin-top: 20px;
        .formContainer{
            width: 100%;
            margin: 0 auto;
           
            .ant-form{
                .ant-form-item{
                   div.ant-form-item-label{
                       margin-right: 8px;
                       min-width: 100px;
                       text-align: right;
                   }
                }
            }

            .ant-form.left{
                .ant-form-item{
                   div.ant-form-item-label{
                       margin-right: 8px;
                       min-width: 100px;
                       text-align: left;
                   }
                }
            }

            .ant-checkbox-wrapper + .ant-checkbox-wrapper{
                margin-left: 0px;
                margin-right: 8px;
                height: 30px;
                line-height: 30px;
            }
        }
    }
    .pagination{
        margin-top: 20px;
    }

    
   

</style>

<template>
    <div class="projects">

        <!-- 筛选区域 -->
        <div class="filter">
            <a-row>
                <a-col :span="7">
                    <div class="casContainer" ref="casContainer">
                        <div class="casInput">
                            <a-input v-model:value="value" placeholder="请选择资源" @focus="did" @change="search">
                                <template #suffix>
                                    <my-icon  type="icon-search-normal"></my-icon>
                                </template>
                            </a-input>
                        </div>

                       <!--  <div class="casbox" :class="[focus? ' fadeIn' : ' fadeOut']" v-if="list.length > 0">
                            <ul class="boxItem ant-cascader-menu">
                                <li @click="select(item)" class="Item ant-cascader-menu-item" v-for="(item, index) in list" :key="index">
                                    {{item.picName}}
                                </li>
                            </ul>
                        </div> -->
                    </div>
                </a-col>
                <a-col :span="2">
                    <a-button @click="showBox= true" type="primary">添加资源</a-button>
                </a-col>

                <a-col :span="10" :offset="5">
                     <a-radio-group v-model:value="size" @change="renderTeams">
                        <a-radio-button value="qiliu">七牛云(无压缩)</a-radio-button>
                        <a-radio-button value="ali">阿里云(有压缩)</a-radio-button>
                    </a-radio-group>
                </a-col>
            </a-row>
        </div>

          
          <!--结果区域 -->
         <div class="result">
           <!--  <a-row :gutter="[16,16]" :justify="center">
                <a-col :span="spanValue" v-for="(item,index) in  page.projects" :key="index"  @mouseenter="item.showDel=true" @mouseleave="item.showDel=false">
                    <div class="content picBlock" :style="{backgroundImage: `url(${item.url})`}">
                        <div style="display:none;">{{item.url}}</div>
                        <div class="del" @click.stop="showDeleteConfirm(item)" v-if="item.showDel">删除</div>
                    </div>
                    <div class="name">{{item.picName}}</div>
                    
                </a-col>
            </a-row> -->
            <div class="list">
                <div v-for="(item,index) in  page.projects" :key="index" :class="['list-line', size === 'small'? 's' : '', size === 'large'? 'g' : '']">
                    <div>{{item.url}}</div>
                    <ul class="actions">
                    <li class="content picBlock">
                        <div style="display:none;">{{item.url}}</div>
                        复制
                    </li>
                    <li @click="showDeleteConfirm(item)">删除</li>
                    </ul>
                </div>
            </div>

            <!-- 分页组件 -->
            <div class="pagination">
                <a-pagination
                    show-size-changer
                    v-model:current="page.curPage"
                    v-model:pageSize="page.pageSize"
                    id="pages"
                    :total="page.totalMount"
                    @change="hideDouleArrow"
                    @showSizeChange="hideDouleArrow"
                 />
            </div>


        </div>

        <!-- 添加box -->
        <a-modal v-model:visible="showBox" title="添加资源" :width="1000">
            <template #footer>
                <a-button key="submit" type="primary" @click="showBox=false">完成</a-button>
            </template>

            <div class="flexForm">
                <div class="formContainer">
                    <a-form ref="formRef" :model="formState"  :label-col="labelCol" :wrapper-col="wrapperCol" style="width: 100%;">
                        <a-row>
                            <a-col :span="24">
                                <Upload :show="showBox" @infoclick="getFileInfo" ></Upload>
                            </a-col>
                        </a-row>
                    </a-form>

                </div>
            </div>
        </a-modal>
    </div>
</template>

<script>
    import {ref,reactive, onMounted, toRaw, createVNode, nextTick, onUnmounted} from 'vue'
    import { useRouter } from "vue-router";
    import Upload from '@/pages/upload.vue'
    import {postRequest} from '@/utils/request';
    import { message } from 'ant-design-vue';
    import { Modal } from 'ant-design-vue';
    import { ExclamationCircleFilled  } from "@ant-design/icons-vue";
    import ClipboardJS from  "clipboard"
    import { useStore, mapState } from 'vuex'


    export default {
        name: 'pics',
        components: {
            Upload
        },
        setup(props) {
         

            const store = useStore()
            
             //初始化路由
            const router = useRouter();
            const showBox = ref(false)
            const list= ref([])
            const curIndex = ref(0)
            const focus = ref(false)
            const curValue = ref('')
            const value= ref('')
            let list_copy = []
            const formRef = ref();
            const formState = reactive({
                picName: '',
                url: ''
            });
            const rules = {
            };
            const size = ref('qiliu')

            let clip  = null

            nextTick( ()=> {
                clip  = new ClipboardJS('.result .picBlock',{
                    text: function(trigger) {
                        message.success(`复制了 ${trigger.childNodes[0].innerText} ！`);
                        return trigger.childNodes[0].innerText
                    }
                })
            })

            const renderTeams = ()=> {
                let type = size.value === 'qiliu'? '七牛云' : '阿里云'
                store.dispatch('saveOssType', type)
            }

            const getPics = async ()=> {
                const {proId} = router.currentRoute.value.query
                console.log(page,'page')
                let {list, totalMount} = await postRequest( `/project/getPics`,{proId, ...page, searchValue: value.value})
                let newList = []
                list = list.reverse()
                /* list.map(item => {
                    if(!newList.some(it => it.url === item.url)){
                        newList.push(item)
                    }
                })
                page.projects = newList */
                 page.projects = list 
                 page.totalMount = totalMount
                list_copy = JSON.parse(JSON.stringify(page.projects))
            }

            onMounted( async (options) =>{
                await getPics()
            })

            const did = () =>  {
                focus.value = !focus.value
                if (focus.value) {
                    curIndex.value = 0
                }
            }

            /* 选中 */
            const select = (data) => {
                value.value= data.picName
                focus.value= false
                page.projects=  list_copy.filter(item  => item.picName === value.value)
            }


            /*  搜索 */
            const search= async() => {
             /*    if(!value.value){
                    list.value =  []
                    page.projects = list_copy
                }else{
                    list.value= list_copy.filter(item =>  item.picName.includes(value.value))
                    page.projects = list.value
                }  */ 
                page.curPage= 1
                await getPics()
            }

            onUnmounted( () => {
                clip && clip.destroy();
            })


            const getFileInfo = async (data) => {
                const {proId} = router.currentRoute.value.query
                const userId = sessionStorage.getItem('userId')
                const {picName, url, hash, key} = data

                try{
                    if(!url){
                        throw new Error('图片上传失败')
                        return;
                    }
                    await postRequest(`/project/addPic`,{userId,  projectId:proId, picName, url, hash, key })
                    message.success('图片上传成功!')
                    /* await getPics() */
                    page.curPage= Math.ceil(page.totalMount / page.pageSize)
                    hideDouleArrow()
                }catch(err){
                    /* message.error(err.meaasge || '图片上传失败！') */
                    console.log(err, 'err')
                }
            }

            const delPic = async (item)=> {
                const {id,projectId} = item
                try{
                    await postRequest(`/project/setNoEffctImg`, {picId : id, projectId: projectId })
                    await getPics()
                    message.success('删除成功！')
                }catch(err){
                    console.log(err, 'err')
                }
                
            }

            const showDeleteConfirm = (data) => {
                Modal.confirm({
                    title: () => '确定要删除这个文件吗?',
                    icon: () => createVNode(ExclamationCircleFilled),
                    content: () => '该资源删除本质为失效，数据库留有记录',
                    okText: () => '确认',
                    okType: 'danger',
                    cancelText: () => '取消',

                    onOk() {
                       delPic(data)
                    },

                    onCancel() {
                       console.log('Cancel');
                    },
                });
            };

     
            const pages = ref('pages')

            const page = reactive({
                 projects: [], // 图片集合
                 pageSize : 10,
                 curPage: 1,
                 totalMount: 0
            }) 

            const  hideDouleArrow = async () => {
                let pages = [].slice.call(document.querySelectorAll('.ant-pagination'))
                let parentEle = pages.find(item => item.id === 'pages')
                let _href =  document.createElement('input')
                _href.className="mf_input_hide"
                if(document.querySelector('.mf_input_hide')){
                    _href = document.querySelector('.mf_input_hide')
                }else{
                    parentEle.appendChild(_href)
                }
                _href.focus();
                _href.style.cssText = `width : 0; height : 0; opacity : 0; border: 0;`

                 await getPics()
            }

            return {
                 page,
                 wh:[],  //左右及上下块边距
                 spanValue: 8, //设置这里展示每行展示 24 / column的向上取值的列
                 value,
                 size,
                 list,
                 curIndex,
                 focus,
                 showBox,
                 curValue,
                 did,
                 select,
                 search,
                 formRef,
                 labelCol: {
                    span: 4,
                 },
                 wrapperCol: {
                    span: 14,
                 },
                 formState,
                 rules,
                 getFileInfo,
                 showDeleteConfirm,
                 renderTeams,
                 hideDouleArrow,
                 pages,
              
            }
        }
    }
</script>

